.card-container {
    display: flex;
    flex-wrap: wrap;
   
    flex-direction: column;
    width: 50%;
  }
  
  .card {
    width: 300px;
    padding: 16px;
    margin: 16px;
    border: 1px solid #ccc;
    border-radius: 4px;
    background-color: rgb(41, 146, 245);
  }

  .first-section{
    background-color: rgb(187, 205, 245);
    grid-template-rows: 50% 50%;
  }
  .card-table{
    width: 50%;
    display: flex;
    flex-wrap: wrap;
   
  }
  .main{
    width: 75%;
    margin: 50px auto;
    border: 1px solid grey;
    display: grid;
    
  }
  .desc-single{
    grid-template-rows: 50% 50%;

  }
  .SingleCourse{
    width: 75%;
    margin: 50px auto;
   
  }
  